<template>
  <a-layout-content style="padding: 0 50px">
    <a-breadcrumb style="margin: 16px 0">
      <a-breadcrumb-item>È¶ñÈ°µ</a-breadcrumb-item>
      <a-breadcrumb-item>ÁôªÈôÜ,Ê≥®ÂÜå</a-breadcrumb-item>
    </a-breadcrumb>
    <div
      :style="{
        background: '#fff',
        padding: '24px',
        minHeight: '620px',
        height: '100%',
      }"
    >
      <a-form-model
        layout="inline"
        :model="formInline"
        @submit.native.prevent
        @submit="onSubmit"
        :rules="rules"
      >
        <a-form-model-item>
          <a-input v-model="formInline.user" placeholder="Email">
            <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-input
            v-model="formInline.password"
            type="password"
            placeholder="Password"
          >
            <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)" />
          </a-input>
        </a-form-model-item>
        <a-form-model-item>
          <a-button
            type="primary"
            html-type="submit"
            :disabled="formInline.user === '' || formInline.password === ''"
          >
            ÁôªÈôÜ
          </a-button>
        </a-form-model-item>
      </a-form-model>
      <a-divider>Ê≥®ÂÜå üëá</a-divider>
      <a-form-model ref="ruleForm" :model="ruleForm" :rules="rules">
        <a-form-model-item has-feedback label="ÈÇÆÁÆ±" prop="email">
          <a-input v-model="ruleForm.email" />
        </a-form-model-item>
        <a-button type="primary" :loading="loading" @click="changeLoading"
          >ÂèëÈÄÅÈ™åËØÅÁ†Å</a-button
        >
        <a-form-model-item has-feedback label="È™åËØÅÁ†Å" prop="validate">
          <a-input v-model.number="ruleForm.validate" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="Password" prop="pass">
          <a-input v-model="ruleForm.pass" type="password" autocomplete="off" />
        </a-form-model-item>
        <a-form-model-item has-feedback label="Confirm" prop="checkPass">
          <a-input
            v-model="ruleForm.checkPass"
            type="password"
            autocomplete="off"
          />
        </a-form-model-item>
        <a-form-model-item>
          <a-button type="primary" @click="submitForm('ruleForm')">
            Ê≥®ÂÜå
          </a-button>
          <a-button style="margin-left: 10px" @click="resetForm('ruleForm')">
            Reset
          </a-button>
        </a-form-model-item>
      </a-form-model>
    </div>
  </a-layout-content>
</template>
<script>
export default {
  data() {
    let checkAge = (rule, value, callback) => {
      if (!value) {
        callback(new Error("ËØ∑ËæìÂÖ•ÈÇÆÁÆ±"));
      } else {
        callback();
      }
    };
    let checkValidate = (rule, value, callback) => {
      if (!value) {
        callback(new Error("ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"));
      } else {
        setTimeout(() => {
          callback();
        }, 1000);
      }
    };
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("ËØ∑ËæìÂÖ•ÂØÜÁ†Å"));
      } else {
        if (this.ruleForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Please input the password again"));
      } else if (value !== this.ruleForm.pass) {
        callback(new Error("Two inputs don't match!"));
      } else {
        callback();
      }
    };
    return {
      rules: {
        pass: [{ validator: validatePass, trigger: "change" }],
        checkPass: [{ validator: validatePass2, trigger: "change" }],
        email: [{ validator: checkAge, trigger: "change" }],
        validate: [{ validator: checkValidate, trigger: "change" }],
      },
      formInline: {
        user: "",
        password: "",
      },
      ruleForm: {
        pass: "",
        checkPass: "",
        email: "",
        validate: "",
      },
      loading: false,
    };
  },
  methods: {
    onSubmit() {
      this.$message.success("ÁôªÈôÜÊàêÂäü");
      this.$store.commit("setNormalState");
      if (this.formInline.user === "zyk@qq.com") {
        this.$store.commit("setLoginState");
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$message.success("ÊàêÂäüÊ≥®ÂÜå!");
          this.$refs[formName].resetFields();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    changeLoading() {
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
        this.$message.success("ÊàêÂäüÂèëÈÄÅ");
      }, 1000);
    },
  },
};
</script>
